"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import SoumettrePage from "../../components/etu_soutenance/soumettre" // Adjust the path if necessary
import { Button } from "@/components/ui/button"
import { Card, CardHeader, CardContent, CardTitle } from "@/components/ui/card"

export default function SoumissionPage() {
  const router = useRouter()
  const [soutenanceId, setSoutenanceId] = useState("12345") // Example ID, replace with dynamic ID if needed

  const handleNavigation = () => {
    router.push(`/pages/soumettre?id=${soutenanceId}`)
  }

  return (
    <Card className="w-full max-w-2xl mx-auto shadow-md">
      <CardHeader>
        <CardTitle>Page de Soumission</CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        <p className="text-gray-700">
          Cliquez sur le bouton ci-dessous pour soumettre vos fichiers pour la soutenance.
        </p>
        <Button onClick={handleNavigation} className="w-full">
          Aller Ã  la soumission
        </Button>
      </CardContent>
    </Card>
  )
}

